* {
  box-sizing: border-box;
  color: white;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 15px;
  font-weight: 700;
  margin: 0;
}

#Container {
  align-items: center;
  background: url("https://us.123rf.com/450wm/pitris/pitris1510/pitris151000024/46619020-space-background-with-stars.jpg?ver=6");
  display: flex;
  flex-direction: column;
  height: 100vh;
  justify-content: center;
}

#Container img {
  height: auto;
  width: 200px;
}

#AreaImage {
  align-items: center;
  display: flex;
  height: 200px;
  justify-content: center;
  width: 100%;
}
